import React, { useState,useEffect } from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import logo from '../images/covid.svg' ;
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import IndiaComponent from './India.component.js';
import WorldComponent from './World.component.js';
import DashboardComponent from './dashboard.component.js';

function MainPageComponent(){

       
        return(
            <div>
                <Router>
                <nav className="navbar navbar-expand-sm bg-dark navbar-dark sticky-top">
                    <img src={logo} style={{height:"40px" , width:"40px"}}></img>
                    <h5 style={{color:"white", alignSelf:"flex-end"}}>COVID-19</h5>
                    
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <Link style={{marginLeft:"100px",color:"#33DDFF"}} to={'/dashboard'} >
                                DASHBOARD
                                </Link>
                            </li>
                            <li className="nav-item">
                                <Link style={{marginLeft:"100px",color:"#FF9933"}} to={'/india'} >
                                INDIA
                                </Link>
                            </li>
                            <li className="nav-item">
                                <Link style={{marginLeft:"100px",color:"#F3F31A"}} to={'/world'}  >
                                WORLD
                                </Link>
                            </li>
                        </ul>
                </nav>
                        
               
                    <Switch>
                        <Route exact path='/dashboard' component={DashboardComponent} />
                        <Route path='/world' component={WorldComponent} />
                        <Route  path='/india' component={IndiaComponent} />
                    </Switch>
                </Router>
            </div>
            )
        

            
}



export default MainPageComponent;
